* {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  object-fit: contain;
  width: auto;
  height: auto;
  opacity: 0.9;
}

.navbar {
  position: fixed;
  height: 80px;
  width: 85%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0);
  font-size: 28px;
  text-align: center;
  color: #351f31;
}

.navbar .logo {
  width: 140px;
  height: auto;
  padding: 50px 50px;
}
.navbar ul {
  float: right;
  margin-right: 20px;
}
.navbar ul li {
  list-style: none;
  margin: 0 8px;
  display: inline-block;
  line-height: 80px;
}
.navbar ul li a {
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  color: white;
  padding: 6px 13px;
  text-decoration: none;
  transition: 0.4s;
}
.navbar ul li a.active,
.navbar ul li a:hover {
  background: #351f31;
  border-radius: 2px;
}
.wrapper .center {
  position: absolute;
  left: 50%;
  top: 55%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: sans-serif;
  -webkit-user-select: none;
          user-select: none;
}

.center h1 {
  color: white;
  font-size: 70px;
  width: 900px;
  font-weight: bold;
  text-align: center;
}
.center h2 {
  color: white;
  font-size: 70px;
  font-weight: bold;
  margin-top: 10px;
  width: 885px;
  text-align: center;
}
.center .buttons {
  margin: 35px 280px;
}
.buttons button {
  height: 50px;
  width: 150px;
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  background: #351f31;
  outline: none;
  cursor: pointer;
  border: 1px solid #351f31;
  border-radius: 25px;
  transition: 0.4s;
}
.buttons .btn2 {
  margin-left: 25px;
}
.buttons button:hover {
  background: red;
}

